export function setStoredOverlayOption(overlayOption) {
    const val = {
        overlayOption,
    };
    return new Promise((resolve) => {
        chrome.storage.local.set(val, () => {
            resolve();
        });
    });
}
export function getStoredOverlayOption() {
    const keys = ['overlayOption'];
    return new Promise((resolve) => {
        chrome.storage.local.get(keys, (res) => {
            resolve(res);
        });
    });
}
